import '../css/main.scss';
import { useState, useRef, useEffect, useCallback } from 'react';
import { useOutletContext, useNavigate } from 'react-router-dom';
import useAutosizeTextarea from '../component/useAutosizeTextarea';
import useSpeechToText from '../component/useSpeechToText';
import axios from 'axios';

//     (ChatRoom  )
const generateChatTitle = (text) => {
    const first = text.split('\n')[0].trim();
    if (!first) return ' ';
    return first.length > 30 ? first.slice(0, 30) + '' : first;
};

const BASE_URL = process.env.REACT_APP_API_BASE_URL || '';
const CHAT_API_URL = `${BASE_URL}/api/chat`;

const Home = () => {
    const {
        activeChatId,
        setActiveChatId,
        chatRooms,
        setChatRooms,
    } = useOutletContext();

    const navigate = useNavigate();
    const textareaRef = useRef(null);

    const [message, setMessage] = useState('');
    const [loading, setLoading] = useState(false);
    const [files, setFiles] = useState([]);
    const [isDragging, setIsDragging] = useState(false);
    const [previewImage, setPreviewImage] = useState(null);
    const { text, setText, isListening, startListening, stopListening } = useSpeechToText();

    useAutosizeTextarea(textareaRef, message);

    const handleInputChange = useCallback((e) => {
        setMessage(e.target.value);
    }, []);

    //   
    const handleFileUpload = (e) => {
        const selected = Array.from(e.target.files || []);
        const filtered = selected.filter((file) => {
            if (file.size > 5 * 1024 * 1024) {
                alert(`${file.name} () 5MB .`);
                return false;
            }
            return true;
        });
        setFiles((prev) => [...prev, ...filtered]);
    };

    const removeFile = (index) => {
        setFiles((prev) => prev.filter((_, i) => i !== index));
    };

    const handleKeyDown = (e) => {
        // Enter   / Shift+Enter  
        if (e.key === 'Enter' && !e.shiftKey) {
            e.preventDefault();
            handleSubmit(e);
        }
    };

    // ---    +  ---
    const sendMessage = async (userMessage, chatId) => {
        setLoading(true);

        const newHistory = [{ role: 'user', content: userMessage }];

        //  title (ttitle)  title 
        setChatRooms((prev) => ({
            ...prev,
            [chatId]: {
                title: generateChatTitle(userMessage),
                history: [
                    ...newHistory,
                    {
                        role: 'ai',
                        content: '  ...',
                        loading: true,
                    },
                ],
            },
        }));

        setActiveChatId(chatId);

        try {
            const response = await axios.post(CHAT_API_URL, {
                messages: newHistory,
            });

            const aiResponse = response.data?.response || '';

            setChatRooms((prev) => {
                const finalHistory = [
                    ...newHistory,
                    {
                        role: 'ai',
                        content: aiResponse,
                        loading: false,
                    },
                ];

                return {
                    ...prev,
                    [chatId]: {
                        ...prev[chatId],
                        history: finalHistory,
                    },
                };
            });
        } catch (err) {
            console.error('API :', err);

            setChatRooms((prev) => ({
                ...prev,
                [chatId]: {
                    ...prev[chatId],
                    history: [
                        ...newHistory,
                        {
                            role: 'ai',
                            content: '  :    .',
                            error: true,
                            loading: false,
                        },
                    ],
                },
            }));
        } finally {
            setLoading(false);
        }
    };

    // ---  submit ---
    const handleSubmit = (e) => {
        e.preventDefault();
        if (loading) return;
        if (!message.trim()) return;

        const chatId = Date.now().toString();
        const userMessage = message.trim();

        setMessage('');

        // ChatRoom  
        navigate(`/chat/${chatId}`, {
            state: {
                initialFiles: files,
            },
        });

        setFiles([]);

        sendMessage(userMessage, chatId);
    };

    //     message  
    useEffect(() => {
        if (text) {
            //      
            setMessage((prev) => (prev ? prev + " " + text : text));

            // :           
            setText('');
        }
    }, [text, setText]);

    return (
        <section className="section_wrap">
            <div className="main_body_wrap">
                <div className="logo" style={{ marginBottom: '5px' }}>
                    <h1>DoAi</h1>
                </div>

                {/*   */}
                <div
                    className={`input_select_wrap ${isDragging ? 'dragging' : ''}`}
                    onDragOver={(e) => {
                        e.preventDefault();
                        setIsDragging(true);
                    }}
                    onDragLeave={() => setIsDragging(false)}
                    onDrop={(e) => {
                        e.preventDefault();
                        setIsDragging(false);

                        const dropped = Array.from(e.dataTransfer.files || []);
                        const filtered = dropped.filter((file) => {
                            if (file.size > 5 * 1024 * 1024) {
                                alert('5MB     .');
                                return false;
                            }
                            return true;
                        });
                        setFiles((prev) => [...prev, ...filtered]);
                    }}
                >
                    {/*   */}
                    {files.length > 0 && (
                        <div className="file_preview_area">
                            {files.map((file, idx) => {
                                const isImage = file.type.startsWith('image/');
                                const previewURL = isImage ? URL.createObjectURL(file) : null;

                                const type = file.type;
                                let icon = '/images/icon_file.png';
                                if (type.includes('pdf')) icon = '/images/icon_pdf.png';
                                else if (
                                    type.includes('word') ||
                                    type.includes('msword') ||
                                    type.includes('doc')
                                )
                                    icon = '/images/icon_doc.png';
                                else if (
                                    type.includes('excel') ||
                                    type.includes('spreadsheet') ||
                                    type.includes('xls')
                                )
                                    icon = '/images/icon_excel.png';
                                else if (type.includes('hwp')) icon = '/images/icon_hwp.png';

                                return (
                                    <div className="file_item" key={idx}>
                                        {isImage ? (
                                            <img
                                                className="thumb"
                                                src={previewURL}
                                                alt={file.name}
                                                onClick={() => setPreviewImage(previewURL)}
                                                style={{ cursor: 'pointer' }}
                                            />
                                        ) : (
                                            <img
                                                className="file_icon"
                                                src={icon}
                                                alt="file icon"
                                            />
                                        )}

                                        <span className="file_name">{file.name}</span>
                                        <button
                                            type="button"
                                            className="file_remove_btn"
                                            onClick={() => removeFile(idx)}
                                        >
                                            
                                        </button>
                                    </div>
                                );
                            })}
                        </div>
                    )}

                    {/*  */}
                    <form onSubmit={handleSubmit}>
                        <div className="input_row">
                            {/*    */}
                            <label className="add_btn">
                                <span className="material-symbols-outlined">attach_file</span>
                                <input
                                    type="file"
                                    multiple
                                    style={{ display: 'none' }}
                                    onChange={handleFileUpload}
                                />
                            </label>

                            {/*   */}
                            <textarea
                                name="message"
                                value={message}
                                ref={textareaRef}
                                onChange={handleInputChange}
                                onKeyDown={handleKeyDown}
                                placeholder=" ."
                            />

                            {/*   */}
                            <button
                                type="button"
                                className={`mic_btn ${isListening ? 'active' : ''}`}
                                onClick={isListening ? stopListening : startListening}
                            >
                                <span className="material-symbols-outlined">
                                    {isListening ? 'mic_off' : 'mic'}
                                </span>
                            </button>

                            {/*   */}
                            <button
                                type="submit"
                                className="send_btn"
                                disabled={!message.trim() || loading}
                            >
                                <span className="material-symbols-outlined">send</span>
                            </button>
                        </div>
                    </form>
                </div>
            </div>

            {/*    */}
            {previewImage && (
                <div
                    className="image_modal_dim"
                    onClick={() => setPreviewImage(null)}
                >
                    <div
                        className="image_modal_wrap"
                        onClick={(e) => e.stopPropagation()}
                    >
                        <img src={previewImage} alt="" />
                    </div>
                </div>
            )}
        </section>
    );
};

export default Home;
