import { useLayoutEffect, useRef } from 'react';

/**
 *    textarea     
 * @param {React.RefObject<HTMLTextAreaElement>} ref - textarea  ref 
 * @param {string} value - textarea   (   )
 */
const useAutosizeTextarea = (ref, value) => {
    useLayoutEffect(() => {
        const textarea = ref.current;

        if (textarea) {
            textarea.style.height = 'auto';
            let scrollHeight = textarea.scrollHeight;
            
            // max-height  (CSS    )
            // : window.getComputedStyle  CSS max-height 
            const computedStyle = window.getComputedStyle(textarea);
            const maxHeight = parseFloat(computedStyle.maxHeight) || Infinity;

            if (scrollHeight > maxHeight) {
                scrollHeight = maxHeight;
                textarea.style.overflowY = 'auto';
            } else {
                textarea.style.overflowY = 'hidden';
            }
            textarea.style.height = scrollHeight + 'px';
        }
    }, [ref, value]);
};

export default useAutosizeTextarea;